















































































































































































































































































































































































































.side-bar.v-navigation-drawer--mini-variant .v-list-group--sub-group {
  display: block !important;
}

.v-application--is-ltr .v-list--dense .v-list-group--sub-group .v-list-group__header {
  padding-left: 0px;
}

.v-application--is-ltr .v-list-group--sub-group .v-list-group__items .v-list-item {
  padding-left: 5px;
}

.v-list-group__items > .v-list-item {
  padding-left: 8px !important;
}

.v-list--nav .v-list-item {
  padding: 0px 5px;
}

.parent-bar > .v-list-group__items {
  background-color: #1e1e1e7d;
  margin: 0 -5px;
}

.v-application--is-ltr .v-list--dense.v-list--nav .v-list-group--no-action > .v-list-group__items > .v-list-item {
  padding-left: 60px !important;
}

.v-application--is-ltr .v-list-item__action:first-child,
.v-application--is-ltr .v-list-item__icon:first-child {
  margin-right: 24px;
}

.radio-btn {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin: auto 0;
}

.radio-input {
  width: 24px;
  height: 21px;
  margin-top: 4px;
}

.text-ellipses {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.rubix-plat-title-container {
  padding: 25px 0;
}

.side-bar {
  position: relative;
}

.lock-icon-button {
  position: absolute;
  top: 66px;
  right: 16px;
}

.list-box-2::-webkit-scrollbar {
  width: 5px;
}

.list-box-2::-webkit-scrollbar-track {
  background: #202020;
  border-left: 1px solid #2c2c2c;
}

.list-box-2::-webkit-scrollbar-thumb {
  background: #3e3e3e;
  border: solid 2px #202020;
  border-radius: 3px;
}

.list-box-2::-webkit-scrollbar-thumb:hover {
  background: white;
}

.mini-device-info {
  font-size: 12px;
  text-align: left;
}

.mini-device-info p {
  margin-bottom: 0px;
}

.mini-device-info .caption {
  margin-bottom: 0px;
}

.mini-device-info .info-title {
  font-size: 12px;
}

.mini-device-info {
  padding-left: 15px;
}

.mini-device-info .overline {
  margin-bottom: -8px;
  font-size: 12px;
}

.switch-device-title {
  font-size: 12px;
  padding-left: 3px;
  text-align: left;
}

.switch-device-title .client-name {
  color: #dfdfdf;
  font-size: 12px;
  text-align: right;
  cursor: pointer;
}

.sidebar-group-item {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
